import React from 'react';
import PropTypes from 'prop-types';
import {
	Panel,
	PanelHeader,
	HeaderButton,
	platform,
	IOS,
	Div,
	View,
	Group,
	Radio,
	Form,
	Card,
	Root,
	Select,
	InfoRow,
	List,
	Cell,
	Link,
	PullToRefresh,
	Button,
	SelectMimicry,
	FormLayout,
	Input,
	Textarea,
	ScreenSpinner
} from '@vkontakte/vkui';
import persik from '../img/persik.png';
import './Persik.css';
import ReactDOM from 'react-dom'
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Done from '@vkontakte/icons/dist/24/done';


const osname = platform();

export default class Persik extends React.Component {

	constructor(props){
	super(props);

	this.state={
		title:"",
		text:"",
		mesto:"",
		setcity:"",
		street:"",
		stattime:"",
		owner_id:"",
		chatlink:"",
		type:"",
		igo:0,
		time:"",
		date:"",
		bookmark:0,
		city:[],
        rr:"ав",
		}


	}
	componentWillMount() {
		const {your_city,city,title,text,mesto,unixtime}=this.props;
		var date="";
		var now=new Date(Number(unixtime)*1000)
		//now.setHours(now.getHours()+2)
		var month=now.getMonth()+1;
		var day=now.getDate();
		if(day<10){day="0"+day}
		if(month<10){month="0"+month}
		var time=""
		var hour=now.getHours()
		if(hour<10){hour="0"+hour}
		var min=now.getMinutes()
		if(min<10){min="0"+min}
		time=hour+":"+min

		date=now.getFullYear().toString()+"-"+month+"-"+day

		this.setState({your_city:your_city,city:city,title:title,text:text,street:mesto.split(", ")[2],setcity:your_city,date:date,time:time})
	}

	get(url){
		var xhr = new XMLHttpRequest();
		xhr.open('POST', url, false);
		xhr.send();
		return xhr.responseText;
	}
	ret2(item,i){

		return(
			<Cell
				onClick={()=>{

					this.setState({activePanel: "home",

					})



				}}
				data-to="home"
				asideContent={this.state.your_city === item ? <Icon24Done fill="var(--accent)" /> : null}
			>
				{item}
			</Cell>

		);
	}
	getCitys(item,i){
		return(
			<option value={item}>{item}</option>
		);
	}
	render() {

		const {go}=this.props;

		return (


			<Panel id="edit">
				<PanelHeader
					left={<HeaderButton onClick={go} data-to="full">{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}</HeaderButton>}>
					Редактирование события
				</PanelHeader>

				<Group>
					<FormLayout><Input defaultValue={this.state.title} top="Заголовок"></Input></FormLayout>
				</Group>
				<Group>
				<FormLayout>
					<Textarea defaultValue={this.state.text} top="Примечание" placeholder="Место, подробности" />
				</FormLayout>
				</Group>
				<Group>
					<FormLayout>
						<Select
							top="Город"
							data-to="other"
							placeholder="Не выбран"
							defaultValue={this.state.your_city}
							onClick={this.go}
						>{this.state.your_city}
							{this.state.city.map(this.getCitys)}
						</Select>
					</FormLayout>
					<FormLayout><Input top="Улица" defaultValue={this.state.street}></Input></FormLayout>
				</Group>


				<Group>
					<FormLayout>
							<Input type="date" defaultValue={this.state.date} top="Дата начала"/>
							<Input type="time" defaultValue={this.state.time} top="Время начала"/>
					</FormLayout>
				</Group>
				<Group>
					<FormLayout>
						<Input type="date" top="Дата окончания"/>
						<Input type="time" top="Время окончания"/>
					</FormLayout>
				</Group>
				<Group>
					<FormLayout>
						<SelectMimicry
							top="Тип"
							data-to="other"
							placeholder="Не выбран"
							onClick={this.go}
						>{this.state.your_city}</SelectMimicry>
					</FormLayout>
				</Group>
				<Group>
					<FormLayout><Input top="Ссылка на группу"></Input></FormLayout>
				</Group>
				<Group>
					<FormLayout><Input top="Ссылка на беседу"></Input></FormLayout>
				</Group>
				<Group>
					<FormLayout>
						<div top="Показывать организатора">
							<Radio name="radio" value="1"  defaultChecked>Да</Radio>
							<Radio name="radio" value="2">Нет</Radio>

						</div>
					</FormLayout>
				</Group>
				<Group>
					<FormLayout>
						<div top="Согласованность с администрацией города" >
							<Radio name="radio" value="1"  defaultChecked>Не согласовано</Radio>
							<Radio name="radio" value="2">Не требуется</Radio>
							<Radio name="radio" value="2">Согласовано</Radio>

						</div>
					</FormLayout>
				</Group>
			</Panel>

		);
	}
}
const style={
	fd: {

		width: '100%',
		display: 'flex',
		height: '100%',
		justifyContent: 'bottom',


	},
	buttons:{
		height: '100%',
		padding: '3px',
		width: '100%',


	},
};
Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};


